import React, { useState } from 'react';
import { 
    Box, 
    Paper, 
    Typography, 
    Button, 
    TextField, 
    Alert,
    Divider,
    Grid
} from '@mui/material';
import { 
    MedicalServices as MedicalIcon,
    Security as SecurityIcon,
    Shortcut as ShortcutIcon
} from '@mui/icons-material';
import { autoWebViewJs } from '@droid/android';

/**
 * 
 * 
 */
const ShortcutConfig: React.FC = () => {
    const [medicalName, setMedicalName] = useState('');
    const [securityName, setSecurityName] = useState('');
    const [isCreating, setIsCreating] = useState(false);
    const [message, setMessage] = useState<{type: 'success' | 'error' | 'info', text: string} | null>(null);

    // 
    const handleCreateMedicalShortcut = async () => {
        if (!medicalName.trim()) {
            setMessage({type: 'error', text: ''});
            return;
        }

        setIsCreating(true);
        setMessage(null);

        try {
            // 
            const supportScript = `com.fanfanlo.emergencycall.manager.ShortcutManager.isShortcutSupported();`;
            const {javaResultString: supportResult} = autoWebViewJs.callScript(supportScript);
            
            if (supportResult !== 'true') {
                setMessage({type: 'error', text: ''});
                return;
            }

            // 
            const script = `com.fanfanlo.emergencycall.manager.ShortcutManager.createMedicalShortcut("${medicalName}");`;
            const {javaResultString} = autoWebViewJs.callScript(script);
            
            if (javaResultString === 'true') {
                setMessage({type: 'success', text: ''});
            } else {
                setMessage({type: 'error', text: ''});
            }
        } catch (error) {
            console.error('Error creating medical shortcut:', error);
            setMessage({type: 'error', text: ''});
        } finally {
            setIsCreating(false);
        }
    };

    // 
    const handleCreateSecurityShortcut = async () => {
        if (!securityName.trim()) {
            setMessage({type: 'error', text: ''});
            return;
        }

        setIsCreating(true);
        setMessage(null);

        try {
            // 
            const supportScript = `com.fanfanlo.emergencycall.manager.ShortcutManager.isShortcutSupported();`;
            const {javaResultString: supportResult} = autoWebViewJs.callScript(supportScript);
            
            if (supportResult !== 'true') {
                setMessage({type: 'error', text: ''});
                return;
            }

            // 
            const script = `com.fanfanlo.emergencycall.manager.ShortcutManager.createSecurityShortcut("${securityName}");`;
            const {javaResultString} = autoWebViewJs.callScript(script);
            
            if (javaResultString === 'true') {
                setMessage({type: 'success', text: ''});
            } else {
                setMessage({type: 'error', text: ''});
            }
        } catch (error) {
            console.error('Error creating security shortcut:', error);
            setMessage({type: 'error', text: ''});
        } finally {
            setIsCreating(false);
        }
    };

    // 
    const handleCheckSupport = async () => {
        try {
            const supportScript = `com.fanfanlo.emergencycall.manager.ShortcutManager.isShortcutSupported();`;
            const brandScript = `com.fanfanlo.emergencycall.manager.ShortcutManager.getDeviceBrand();`;
            const guideScript = `com.fanfanlo.emergencycall.manager.ShortcutManager.getPermissionGuideText();`;
            
            const {javaResultString: supportResult} = autoWebViewJs.callScript(supportScript);
            const {javaResultString: brandResult} = autoWebViewJs.callScript(brandScript);
            const {javaResultString: guideResult} = autoWebViewJs.callScript(guideScript);
            
            const supportInfo = `
: ${brandResult}
: ${supportResult === 'true' ? '' : ''}

${guideResult}`;
            
            setMessage({type: 'info', text: supportInfo});
        } catch (error) {
            console.error('Error checking support:', error);
            setMessage({type: 'error', text: ''});
        }
    };
    
    // 
    const handleRequestPermission = async () => {
        try {
            console.log('...');
            
            // 
            const brandScript = `com.fanfanlo.emergencycall.manager.ShortcutManager.getDeviceBrand();`;
            const brandResult = autoWebViewJs.callScript(brandScript);
            console.log(':', brandResult);
            
            if (!brandResult || brandResult.javaResultString === undefined) {
                throw new Error('');
            }
            
            // 
            const script = `com.fanfanlo.emergencycall.manager.ShortcutManager.checkAndRequestPermissionWithResult();`;
            const {javaResultString: result} = autoWebViewJs.callScript(script);
            console.log(':', result);
            
            if (result && result !== 'undefined') {
                setMessage({
                    type: result.startsWith('') ? 'success' : result.startsWith('') ? 'error' : 'info', 
                    text: result
                });
            } else {
                throw new Error('undefined');
            }
        } catch (error) {
            console.error(':', error);
            console.log('...');
            
            //  + 
            try {
                const fallbackScript = `com.fanfanlo.emergencycall.manager.ShortcutManager.checkAndRequestShortcutPermission();`;
                const fallbackResult = autoWebViewJs.callScript(fallbackScript);
                
                const brand = autoWebViewJs.callScript(`com.fanfanlo.emergencycall.manager.ShortcutManager.getDeviceBrand();`);
                const brandName = brand?.javaResultString || 'UNKNOWN';
                
                let guideText = '';
                if (brandName === 'XIAOMI' || brandName === 'REDMI') {
                    guideText = `\n\n/Redmi\n1. "" > "" > ""\n2. ""\n3. "" > ""\n4. ""`;
                } else {
                    guideText = `\n\n\n >  >  > \n""`;
                }
                
                setMessage({
                    type: 'warning', 
                    text: ` \n\n${guideText}\n\n `
                });
            } catch (fallbackError) {
                console.error(':', fallbackError);
                setMessage({
                    type: 'error', 
                    text: ' \n\n\n\n\n1.  >  > \n2. ""\n3. '
                });
            }
        }
    };

    return (
        <Box sx={{ p: 3 }}>
            <Paper elevation={2} sx={{ p: 3, mb: 3 }}>
                <Box sx={{ display: 'flex', alignItems: 'center', mb: 2 }}>
                    <ShortcutIcon sx={{ mr: 2, color: 'primary.main' }} />
                    <Typography variant="h5" component="h1">
                        
                    </Typography>
                </Box>
                
                <Typography variant="body2" color="text.secondary" sx={{ mb: 3 }}>
                    
                </Typography>

                {message && (
                    <Alert 
                        severity={message.type} 
                        sx={{ mb: 3 }}
                        onClose={() => setMessage(null)}
                    >
                        <pre style={{ whiteSpace: 'pre-wrap', margin: 0 }}>
                            {message.text}
                        </pre>
                    </Alert>
                )}

                <Grid container spacing={3}>
                    {/*  */}
                    <Grid item xs={12} md={6}>
                        <Paper variant="outlined" sx={{ p: 3 }}>
                            <Box sx={{ display: 'flex', alignItems: 'center', mb: 2 }}>
                                <MedicalIcon sx={{ mr: 2, color: 'error.main' }} />
                                <Typography variant="h6"></Typography>
                            </Box>
                            
                            <Typography variant="body2" color="text.secondary" sx={{ mb: 2 }}>
                                
                            </Typography>

                            <TextField
                                fullWidth
                                label=""
                                value={medicalName}
                                onChange={(e) => setMedicalName(e.target.value)}
                                sx={{ mb: 2 }}
                                placeholder=""
                            />

                            <Button
                                fullWidth
                                variant="contained"
                                color="error"
                                onClick={handleCreateMedicalShortcut}
                                disabled={isCreating}
                                startIcon={<MedicalIcon />}
                            >
                                {isCreating ? '...' : ''}
                            </Button>
                        </Paper>
                    </Grid>

                    {/*  */}
                    <Grid item xs={12} md={6}>
                        <Paper variant="outlined" sx={{ p: 3 }}>
                            <Box sx={{ display: 'flex', alignItems: 'center', mb: 2 }}>
                                <SecurityIcon sx={{ mr: 2, color: 'warning.main' }} />
                                <Typography variant="h6"></Typography>
                            </Box>
                            
                            <Typography variant="body2" color="text.secondary" sx={{ mb: 2 }}>
                                
                            </Typography>

                            <TextField
                                fullWidth
                                label=""
                                value={securityName}
                                onChange={(e) => setSecurityName(e.target.value)}
                                sx={{ mb: 2 }}
                                placeholder=""
                            />

                            <Button
                                fullWidth
                                variant="contained"
                                color="warning"
                                onClick={handleCreateSecurityShortcut}
                                disabled={isCreating}
                                startIcon={<SecurityIcon />}
                            >
                                {isCreating ? '...' : ''}
                            </Button>
                        </Paper>
                    </Grid>
                </Grid>

                <Divider sx={{ my: 3 }} />

                {/*  */}
                <Box sx={{ display: 'flex', gap: 2, justifyContent: 'center', flexWrap: 'wrap' }}>
                    <Button
                        variant="outlined"
                        onClick={handleCheckSupport}
                        disabled={isCreating}
                    >
                        
                    </Button>
                    
                    <Button
                        variant="outlined"
                        color="primary"
                        onClick={handleRequestPermission}
                        disabled={isCreating}
                    >
                        
                    </Button>
                </Box>

                {/*  */}
                <Box sx={{ mt: 3, p: 2, bgcolor: 'grey.50', borderRadius: 1 }}>
                    <Typography variant="subtitle2" gutterBottom>
                        
                    </Typography>
                    <Typography variant="body2" component="div">
                        <ul style={{ margin: 0, paddingLeft: '20px' }}>
                            <li></li>
                            <li></li>
                            <li></li>
                            <li></li>
                            <li><strong></strong>OPPOVIVO</li>
                            <li></li>
                        </ul>
                    </Typography>
                    
                    <Box sx={{ mt: 2, p: 1.5, bgcolor: 'warning.light', borderRadius: 1, border: '1px solid', borderColor: 'warning.main' }}>
                        <Typography variant="body2" color="warning.dark">
                            <strong> </strong>
                            
                            
                        </Typography>
                    </Box>
                </Box>
            </Paper>
        </Box>
    );
};

export default ShortcutConfig;